.middleDiv {
    height: 53px;
    padding-top: 50px;
    padding-left: 300px;
    padding-right: 300px;
    padding-bottom: 270px;
}

.searchDiv {
    padding-left: 300px;
    padding-right: 300px;
    background-color: rgb(240, 240, 240);
}

.classListSpan {
    font-size: 20px;
}

.classDiv {
    margin-top: 50px;
}

.classSpan {
    font-size: 20px;
}

#searchText {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 20px;
}

.fileNameInput {
    width: 500px;
    margin-top: 20px;
    margin-right: 20px;
}

.classNo {
    margin-top: 20px;
}

.fileNameInput,
.classNo {
    display: inline-block;
    padding: 0.5em 0.75em;
    font-size: inherit;
    font-family: inherit;
    line-height: normal;
    vertical-align: middle;
    background-color: #f5f5f5;
    border: 1px solid #ebebeb;
    border-bottom-color: #e2e2e2;
    border-radius: 0.25em;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.middleDiv label {
    display: inline-block;
    padding: 0.5em 0.75em;
    color: #999;
    font-size: inherit;
    line-height: normal;
    vertical-align: middle;
    background-color: #fdfdfd;
    cursor: pointer;
    border: 1px solid #ebebeb;
    border-bottom-color: #e2e2e2;
    border-radius: 0.25em;
    margin-top: 20px;
}

.middleDiv input[type="file"] {
    /* 파일 필드 숨기기 */
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}

.middleDiv label {
    color: #fff;
    background-color: #5cb85c;
    border-color: #4cae4c;
}

.searchBtn {
    margin-top: 30px;
    color: #fff;
    background-color: #5cb85c;
    padding: 0.5em 0.75em;
    cursor: pointer;
    border: 1px solid #ebebeb;
    border-bottom-color: #e2e2e2;
    border-radius: 0.25em;
    font-size: inherit;
}

.footer {
    text-align: center;
    padding-top: 10px;
    color: #5cb85c;
}
